import React,{useState} from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import InputBase from '@mui/material/InputBase';
import SearchIcon from '@mui/icons-material/Search';
import { Tabs ,Tab,Button,alpha,styled} from '@mui/material';
import DrawerComponent from './DrawerComponent';
// import Product from '../pages/Product';

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(1),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));
const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '25ch',
      '&:focus': {
        width: '35ch',
      },
    },
  },
}));

const PAGES = ['Products','Services','ContactUs','About Us']


const NavBar = () => {
  const [value, setValue] = useState();
 
  return (
    <React.Fragment>
    <AppBar>
        <Toolbar>
            <DrawerComponent/>
            <Tabs textColor='inherit' value={value} onChange={(e,value)=>setValue(value)} indicatorColor='secondary' sx={{ display: { xs: 'none', sm: 'none', md: 'block' }}}>
            {PAGES.map((page, index)=>(
              <Tab key={index} label={page}/>
            ))}
            </Tabs>
              <Search sx={{ display: { xs: 'none', sm: 'block' } }}>
              <SearchIconWrapper >
                <SearchIcon />
              </SearchIconWrapper>
              <StyledInputBase
                placeholder="Search…"
                inputProps={{ 'aria-label': 'search' }}
              />
            </Search>
            <Button sx={{marginLeft:'auto'}} variant="contained">Login</Button>
            <Button sx={{marginLeft:'10px'}} variant="contained">SignUp</Button>
        </Toolbar>
    </AppBar>
    </React.Fragment>
  );
};
export default NavBar;
