import React, { useState,useEffect } from "react";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import { useNavigate, useParams} from "react-router-dom";
import Typography from "@mui/material/Typography";
import { useDispatch ,useSelector} from "react-redux";
import { getSingleUser, updateUser } from "../redux/actions";

const EditUser = () => {
  const navigate = useNavigate();
  let {id} = useParams();
  const [userData, setUserData] = useState({
    name: "",
    username: "",
    email: "",
  });

  let dispatch = useDispatch();
  let {user} = useSelector((user) => user.data);
  const [error, setError] = useState(" ");

  const { name, username, email } = userData;

  useEffect(()=>{
    dispatch(getSingleUser(id))
  },[])


  useEffect(()=>{
    if(user){
        setUserData({...user})
    }
  },[user])


  const handleInputChange = (e) => {
    setUserData({
      ...userData,
      [e.target.name]: e.target.value,
    });
  };


  const handerUpdate = (e) => {
    e.preventDefault();
    if (!name || !email || !username) {
      setError("Please fill the All Input");
    } else {
      dispatch(updateUser(userData,id));
      navigate("/");
      setError("");
    }
  };

  return (
    <Box>
      <h2 align="center" style={{ marginTop: "3rem" }}>
        Edit User
      </h2>
      {error && (
        <h4 align="center" style={{ color: "red" }}>
          {error}
        </h4>
      )}
      <Box
        component="form"
        onSubmit={handerUpdate}
        sx={{
          "& > :not(style)": { m: 1, width: "50ch" },
          marginTop: "5%",
          marginLeft: "35%",
        }}
        noValidate
        autoComplete="off"
      >
        <TextField
          id="standard-basic"
          label="Name"
          variant="standard"
          name="name"
          value={name || ""}
          type="text"
          onChange={handleInputChange}
        />
        <br />
        <br />
        <TextField
          id="standard-basic"
          variant="standard"
          value={username || ""}
          label="Username"
          name="username"
          type="text"
          onChange={handleInputChange}
        />
        <br />
        <br />
        <TextField
          id="standard-basic"
          variant="standard"
          label="Email"
          value={email || ""}
          name="email"
          type="text"
          onChange={handleInputChange}
        />
        <br />
        <br />
        <Typography>
          <Button
            variant="contained"
            color="secondary"
            onClick={() => navigate("/")}
          >
            Go back
          </Button>
          <Button
            style={{ marginLeft: "13rem" }}
            variant="contained"
            color="primary"
            type="submit"
          >
            Update
          </Button>
        </Typography>
      </Box>
    </Box>
  );
};

export default EditUser;
